<template>
  <div class="portfolio">
    <div class="page-container">
      <header class="portfolio-header">
        <h1 class="page-title">作品展示</h1>
        <p class="page-description">我的绘画与摄影作品集</p>
      </header>

      <!-- 筛选器 -->
      <section class="filters">
        <t-space>
          <t-select v-model="selectedCategory" placeholder="选择分类" @change="filterWorks">
            <t-option value="all" label="全部分类" />
            <t-option value="painting" label="绘画" />
            <t-option value="photography" label="摄影" />
            <t-option value="digital" label="数字艺术" />
            <t-option value="cityscape" label="城市印象" />
          </t-select>
          
          <t-select v-model="selectedTime" placeholder="时间筛选" @change="filterWorks">
            <t-option value="all" label="全部时间" />
            <t-option value="2024" label="2024年" />
            <t-option value="2023" label="2023年" />
            <t-option value="2022" label="2022年" />
          </t-select>
        </t-space>
      </section>

      <!-- 作品集合展示 -->
      <section class="collections">
        <!-- 摄影作品集合 -->
        <div v-if="photographyWorks.length > 0" class="collection">
          <div class="collection-header">
            <div class="collection-info">
              <h2 class="collection-title">摄影作品</h2>
              <p class="collection-desc">记录生活中的光影瞬间，捕捉时间凝固的美好</p>
            </div>
            <div class="collection-stats">
              <span class="stats-item">{{ photographyWorks.length }} 作品</span>
            </div>
          </div>
          
          <div class="collection-grid">
            <div 
              v-for="work in photographyWorks" 
              :key="work.id" 
              class="collection-item"
              @click="viewWork(work.id)"
            >
              <div class="item-image">
                <img :src="getWorkImage(work)" :alt="work.title" />
                <div class="item-overlay">
                  <h3>{{ work.title }}</h3>
                  <p>{{ work.description }}</p>
                </div>
              </div>
              <div class="item-info">
                <span class="item-title">{{ work.title }}</span>
                <span class="item-tags">
                  <t-tag v-for="tag in work.tags.slice(0, 2)" :key="tag" size="small" theme="default" variant="light">
                    {{ tag }}
                  </t-tag>
                </span>
              </div>
            </div>
          </div>
        </div>

        <!-- 绘画作品集合 -->
        <div v-if="paintingWorks.length > 0" class="collection">
          <div class="collection-header">
            <div class="collection-info">
              <h2 class="collection-title">绘画作品</h2>
              <p class="collection-desc">用画笔描绘内心世界，展现艺术创作的无限可能</p>
            </div>
            <div class="collection-stats">
              <span class="stats-item">{{ paintingWorks.length }} 作品</span>
            </div>
          </div>
          
          <div class="collection-grid">
            <div 
              v-for="work in paintingWorks" 
              :key="work.id" 
              class="collection-item"
              @click="viewWork(work.id)"
            >
              <div class="item-image">
                <img :src="getWorkImage(work)" :alt="work.title" />
                <div class="item-overlay">
                  <h3>{{ work.title }}</h3>
                  <p>{{ work.description }}</p>
                </div>
              </div>
              <div class="item-info">
                <span class="item-title">{{ work.title }}</span>
                <span class="item-tags">
                  <t-tag v-for="tag in work.tags.slice(0, 2)" :key="tag" size="small" theme="default" variant="light">
                    {{ tag }}
                  </t-tag>
                </span>
              </div>
            </div>
          </div>
        </div>

        <!-- 数字艺术作品集合 -->
        <div v-if="digitalWorks.length > 0" class="collection">
          <div class="collection-header">
            <div class="collection-info">
              <h2 class="collection-title">数字艺术</h2>
              <p class="collection-desc">探索数字媒介的艺术表达，创造独特的视觉体验</p>
            </div>
            <div class="collection-stats">
              <span class="stats-item">{{ digitalWorks.length }} 作品</span>
            </div>
          </div>
          
          <div class="collection-grid">
            <div 
              v-for="work in digitalWorks" 
              :key="work.id" 
              class="collection-item"
              @click="viewWork(work.id)"
            >
              <div class="item-image">
                <img :src="getWorkImage(work)" :alt="work.title" />
                <div class="item-overlay">
                  <h3>{{ work.title }}</h3>
                  <p>{{ work.description }}</p>
                </div>
              </div>
              <div class="item-info">
                <span class="item-title">{{ work.title }}</span>
                <span class="item-tags">
                  <t-tag v-for="tag in work.tags.slice(0, 2)" :key="tag" size="small" theme="default" variant="light">
                    {{ tag }}
                  </t-tag>
                </span>
              </div>
            </div>
          </div>
        </div>

        <!-- 城市印象作品集合 -->
        <div v-if="cityscapeWorks.length > 0" class="collection">
          <div class="collection-header">
            <div class="collection-info">
              <h2 class="collection-title">城市印象</h2>
              <p class="collection-desc">记录城市风光，展现现代都市的多元魅力</p>
            </div>
            <div class="collection-stats">
              <span class="stats-item">{{ cityscapeWorks.length }} 作品</span>
            </div>
          </div>
          
          <div class="collection-grid">
            <div 
              v-for="work in cityscapeWorks" 
              :key="work.id" 
              class="collection-item"
              @click="viewWork(work.id)"
            >
              <div class="item-image">
                <img :src="getWorkImage(work)" :alt="work.title" />
                <div class="item-overlay">
                  <h3>{{ work.title }}</h3>
                  <p>{{ work.description }}</p>
                </div>
              </div>
              <div class="item-info">
                <span class="item-title">{{ work.title }}</span>
                <span class="item-tags">
                  <t-tag v-for="tag in work.tags.slice(0, 2)" :key="tag" size="small" theme="default" variant="light">
                    {{ tag }}
                  </t-tag>
                </span>
              </div>
            </div>
          </div>
        </div>
      </section>

      <!-- 空状态 -->
      <div v-if="hasNoResults" class="empty-state">
        <div class="empty-icon">
          <t-icon name="image" size="80px" />
        </div>
        <h3>暂无相关作品</h3>
        <p>请尝试调整筛选条件或稍后再来查看</p>
        <t-button theme="primary" @click="resetFilters">重置筛选</t-button>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { useWorksStore } from '@/stores/works'
import { imageConfig } from '@/data/images'

const router = useRouter()
const worksStore = useWorksStore()

const selectedCategory = ref('all')
const selectedTime = ref('all')

// 使用 store 中的作品数据
const works = computed(() => worksStore.publishedWorks)

// 初始化作品数据
onMounted(() => {
  worksStore.initWorks()
})

// 按分类获取作品
const photographyWorks = computed(() => getCategoryWorks('photography'))
const paintingWorks = computed(() => getCategoryWorks('painting'))
const digitalWorks = computed(() => getCategoryWorks('digital'))
const cityscapeWorks = computed(() => getCategoryWorks('cityscape'))

// 按分类获取作品的通用函数
const getCategoryWorks = (category: string) => {
  if (!works.value || works.value.length === 0) {
    return []
  }
  
  return works.value.filter(work => {
    if (!work) return false
    
    const categoryMatch = work.category === category
    const timeMatch = selectedTime.value === 'all' || 
                   new Date(work.createdAt).getFullYear().toString() === selectedTime.value
    const categoryFilterMatch = selectedCategory.value === 'all' || selectedCategory.value === category
    
    return categoryMatch && timeMatch && categoryFilterMatch
  })
}

// 是否没有搜索结果
const hasNoResults = computed(() => {
  return photographyWorks.value.length === 0 &&
         paintingWorks.value.length === 0 &&
         digitalWorks.value.length === 0 &&
         cityscapeWorks.value.length === 0
})

const getWorkImage = (work: any) => {
  if (!work) return 'https://picsum.photos/seed/error/300/200.jpg'
  
  // 优先使用作品的缩略图
  if (work.thumbnail) {
    return work.thumbnail
  }
  
  // 如果没有缩略图，尝试从 imageConfig 获取默认图片
  if (work.category) {
    const configImages = imageConfig.works[work.category as keyof typeof imageConfig.works]
    if (configImages && configImages.length > 0) {
      return configImages[0].thumbnail
    }
  }
  
  // 最后使用占位图
  return 'https://picsum.photos/seed/default/300/200.jpg'
}

const filterWorks = () => {
  console.log('Filters applied:', {
    category: selectedCategory.value,
    time: selectedTime.value
  })
}

const viewWork = (id: string) => {
  router.push(`/portfolio/${id}`)
}

const resetFilters = () => {
  selectedCategory.value = 'all'
  selectedTime.value = 'all'
}
</script>

<style lang="scss" scoped>
