  padding: $spacing-xl 0;
}

.portfolio-header {
  text-align: center;
  margin-bottom: $spacing-xl;
  
  .page-title {
    font-size: 2.5rem;
    font-weight: 700;
    color: $primary-color;
    margin-bottom: $spacing-md;
  }
  
  .page-description {
    font-size: 1.1rem;
    color: $text-secondary;
  }
}

.filters {
  margin-bottom: $spacing-xl;
  display: flex;
  justify-content: center;
}

.collections {
  .collection {
    margin-bottom: $spacing-xxl;
    
    .collection-header {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: $spacing-xl;
      padding-bottom: $spacing-md;
      border-bottom: 2px solid $primary-color;
      
      .collection-info {
        .collection-title {
          font-size: 2rem;
          font-weight: 600;
          color: $primary-color;
          margin-bottom: $spacing-sm;
        }
        
        .collection-desc {
          color: $text-secondary;
          font-size: 1.1rem;
          line-height: 1.6;
          max-width: 600px;
        }
      }
      
      .collection-stats {
        .stats-item {
          background: $primary-color;
          color: white;
          padding: 8px 16px;
          border-radius: 20px;
          font-size: 0.9rem;
          font-weight: 500;
        }
      }
    }
    
    .collection-grid {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
      gap: $spacing-lg;
      
      .collection-item {
        background: $glass-bg;
        backdrop-filter: blur(10px);
        border: 1px solid $glass-border;
        border-radius: 16px;
        overflow: hidden;
        cursor: pointer;
        transition: all $transition-normal ease;
        
        &:hover {
          transform: translateY(-8px);
          box-shadow: $shadow-lg;
          
          .item-overlay {
            opacity: 1;
          }
        }
        
        .item-image {
          position: relative;
          aspect-ratio: 4/3;
          overflow: hidden;
          
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: transform $transition-normal ease;
          }
          
          .item-overlay {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: linear-gradient(to bottom, rgba(0,0,0,0.7), rgba(0,0,0,0.9));
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            color: white;
            opacity: 0;
            transition: opacity $transition-normal ease;
            padding: $spacing-lg;
            text-align: center;
            
            h3 {
              font-size: 1.2rem;
              font-weight: 600;
              margin-bottom: $spacing-sm;
            }
            
            p {
              font-size: 0.9rem;
              line-height: 1.4;
            }
          }
        }
        
        &:hover img {
          transform: scale(1.05);
        }
        
        .item-info {
          padding: $spacing-md;
          display: flex;
          justify-content: space-between;
          align-items: center;
          gap: $spacing-sm;
          
          .item-title {
            flex: 1;
            font-weight: 600;
            color: $text-primary;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          
          .item-tags {
            display: flex;
            gap: $spacing-xs;
            flex-shrink: 0;
          }
        }
      }
    }
  }
}

.empty-state {
  text-align: center;
  padding: $spacing-xxl 0;
  
  .empty-icon {
    margin-bottom: $spacing-lg;
    color: $text-tertiary;
  }
  
  h3 {
    font-size: 1.5rem;
    color: $text-primary;
    margin-bottom: $spacing-md;
  }
  
  p {
    color: $text-secondary;
    margin-bottom: $spacing-xl;
  }
}

@media (max-width: $breakpoint-lg) {
  .collection-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr)) !important;
    gap: $spacing-md !important;
  }
  
  .collection-header {
    flex-direction: column;
    align-items: flex-start !important;
    gap: $spacing-md;
    
