}

@media (max-width: $breakpoint-md) {
  .portfolio {
    padding: $spacing-lg 0;
  }
  
  .portfolio-header {
    margin-bottom: $spacing-lg;
    
    .page-title {
      font-size: 2rem;
    }
  }
  
  .collection-grid {
    grid-template-columns: 1fr !important;
    gap: $spacing-md !important;
  }
  
  .collection-item {
    .item-info {
      flex-direction: column;
      align-items: flex-start !important;
      gap: $spacing-sm;
      
      .item-title {
        white-space: normal;
      }
    }
  }
}
</style>